export default class util {
	constructor() {
		this.store = {
			userInfo: {},
			nowTask: {},
			nowReceive: {},
			recommendFrom: ''
		}
		// this.taskStatusType = [
		// 	'', '', '', '', '', '',','
		// ]
		this.taskStatusType = {
			'PASSED': '',
			'DOING': '',
			'FINISHED': '',
			'ALLEGE': '',
			'ALLEGE-SUCCESSED': '',
			'ALLEGE-FAILED': '',
			'SUBMIT': ',',
			'REJECTED': ''
		}
		this.statusColor = {
			'': 'red',
			'': '',
			'': '',
			'': '',
			'': '',
			'': ''
		}
	}
	showToast(text, icon, cb) {
		uni.showToast({
			title: text,
			duration: 1500,
			icon: icon || 'none',
			complete() {
				setTimeout(() => {
					cb && cb()
				}, 1500)
			}
		});
	}
	http(funcName, data) {
		let info = this.store.userInfo
		return new Promise((resolve, reject) => {
			uni.showLoading({
				title: ''
			});
			uniCloud.callFunction({
					name: 'task',
					data: {
						funcName,
						userInfo: info,
						...data
					}
				})
				.then(res => {
					uni.hideLoading()
					if (!res.result.err) {
						resolve(res)
					} else {
						uni.showToast({
							title: res.result.err,
							duration: 2000,
							icon: 'none'
						});
						reject()
					}
				})
				.catch(err => {
					uni.hideLoading()
					console.log('err', err)
					uni.showToast({
						title: '',
						duration: 2000,
						icon: 'none'
					});
				})
		})
	}
	async getUserInfo() {
		let ret = await this.http('getUserInfo', Object.assign(this.store.userInfo, {
			recommendFrom: this.store.recommendFrom
		}))
		if (ret.result.openId) {
			this.store.userInfo = ret.result
			// 
			if (new Date().getTime() - ret.result.todayFirstLoginTime < 1000) {
				uni.showToast({
					title: '+1+1',
					duration: 2000,
					icon: 'none'
				});
			}
			return ret.result
		} else {
			uni.navigateTo({
				url: `/pages/login/login`
			});
		}
		// return new Promise((resolve, reject) => {
		// 	uni.getUserInfo({
		// 		success: async (res) => {
		// 			console.log('uni-getuser', res)
		// 			this.store.userInfo.nickName = res.userInfo.nickName
		// 			this.store.userInfo.avatarUrl = res.userInfo.avatarUrl
		// 			let ret = await this.http('getUserInfo', this.store.userInfo)
		// 			this.store.userInfo = ret.result.data[0]
		// 			resolve(this.store.userInfo)
		// 		},
		// 		fail: (res) => {
		// 			uni.navigateTo({
		// 				url: `/pages/login/login`
		// 			});
		// 			reject()
		// 		}
		// 	})
		// })
	}
	previewImg(urls) {
		uni.previewImage({
			urls: urls
		})
	}
}
